<template>
	<div class="chart-container">
	
		<canvas :id="chartId"></canvas>
	
	</div>
</template>

<script>
import { mapState } from 'vuex'
import {bs4TextColor,chartColor} from '@/conf/common.conf.js'

import Chart from 'chart.js'

//labelschartColor
//chartColor.labels.length
//chartColor.labels
//labels
let getDataColor=(labels,colorObj={})=>{
	const lenDefault=chartColor.labels.length
	const clrNameArr=chartColor.labels
	
	let minus=labels.length-lenDefault
	let len=minus<=0?labels.length:lenDefault
	let isEnd=minus<=0?true:false
	let name=''
	let clrObjDefault={names:[],background:[],border:[]}
	
	colorObj=Object.assign({},clrObjDefault,colorObj)
	
	for(var i=0;i<len;i++){
		name=clrNameArr[i]
		colorObj.names.push(name)
		colorObj.background.push(chartColor['background'][name])
		colorObj.border.push(chartColor['border'][name])
	}
	
	//
	if(isEnd){		
		//console.log(colorObj)
		return colorObj
	}
	
	labels=labels.slice(len)
	//console.log(labels)
	
	return getDataColor(labels,colorObj)
	//0~lenlen
	//name=clrNameArr[Math.floor(Math.random() * Math.floor(len))]
}

const dataColor=getDataColor(chartColor.labels)

let data={
		labels: dataColor.names,
		datasets: [{
			label:'',
			data: [12, 19, 3, 5, 2, 3],
			backgroundColor: dataColor.background,
			borderColor: dataColor.border,
			borderWidth: 1,
			barPercentage: 0.5,
			categoryPercentage: 1,
		}]
	}

let options={
		scales: {
			yAxes: [{
				gridLines: {
					display: true,
				},
				ticks: {
					//stacked: true,
					//beginAtZero: true,
					fontSize:16,
					//suggestedMin:0,
				},
				
			}],
			xAxes: [{
				gridLines: {
					display: true
				},
				ticks: {
					//stacked: true,
					beginAtZero: true,
					suggestedMin:0,
				},
			}]
		},
		title: {
            display: true,
            text: 'Custom Chart Title',
			fontSize:20,
			fontColor:''
        },
		maintainAspectRatio: false,
	}

export default {
	name: 'TheChart',
	data:function(){
		return {
			
			/*//chart
			chartData:data,
			chartOptions:options
			*/
		}
	},
	props: {
		chartId:{
			type:String,
			default:'myChart',
			required:true
		},
		chartType:{
			type:String,
			default:'bar',
			required:true
		}
	},
	computed:{
		//this
		chart:function(){
			return new Chart(this.chartId, {
					type:this.chartType,
					/*//datachart
					data:this.chartData,
					options:this.chartOptions */
					//
					data:data,
					options:options
				})
		},
		...mapState({
			items:state=>state.sideActive.items,
			chartTitle:state=>state.entity.label,
			themeClr:state=>{
				let clrStr=state.navActive.themeClr
				let clrObj=bs4TextColor
				return clrObj.hasOwnProperty(clrStr)?clrObj[clrStr]:''
			}
		}),
	},
	watch:{
		chartTitle:function(){
		//chart:function(){
			this.updateChart()
		}
	},
	methods:{
		updateChart(){
			let myChart=this.chart
			let items=this.items
			
			let data=myChart.data
			let title=myChart.options.title
			
			let chartDataLabels=items.hasOwnProperty('labels')?
									items['labels']:['xx']
			let chartDataSetsData=items.hasOwnProperty('totals')?
									items['totals']:['xx']

			title.text=this.chartTitle
			title.fontColor=this.themeClr
			data.labels=chartDataLabels
			data.datasets[0].data=chartDataSetsData
			
			return myChart.update()
		}
	},
	mounted(){
		/* 
		//vue
		console.log(this.$el)
		console.log(this.$props)
		console.log(this.$data)
		console.log(this.$options)
		*/
		this.updateChart()
		//console.log('mounted')
	},
}
</script>

